@use '../1-helpers/variables' as v;
/*@use '../1-helpers/mixins' as *;*/

*,
*:before,
*:after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  background: v.$clr-primary no-repeat;
  max-width: 100vw;
  height: 100vh;
  margin-bottom: 10vw;
}

@media screen and (prefers-reduced-motion: no-preference) {
  html {
    scroll-behavior: smooth;
  }
}

body {
  max-height: 100%;
  position: relative;
  margin: 0 0 v.$header-h 0;
  height: 100%;
  color: v.$clr-text;
  overflow-x: hidden;
  &:before {
    /*background: transparent no-repeat;*/
    content: "";
    height: 100vh;
    width: 100%;
  }
}

main {
  position: relative;
}

#wrapper {
  width: 100vw;
  position: relative;
  /*margin: 10vw;*/
  //TODO move wrapper div above footer
  background: v.$clr-background;
}

footer {
  margin-top: auto;
  margin-bottom: 0;
  padding: 0;
}

div.scroll {
/*  width: 600px;
  height: 150px;*/
  overflow-x: hidden;
  overflow-y: auto;
  /*text-align: center;*/
  /*padding: 20px;*/
}

img {
  display: block;
  max-width: 100%;
  background-repeat: no-repeat;
}

.icon svg {
  height: 1em;
  width: 1em;
}

hr {
  border-top: 1px solid v.$clr-text;
  opacity: .06;
}

abbr {
  position: relative;
  color: v.$clr-primary;
  text-decoration: none;
  padding: 0 .15rem;
  cursor: help;
}

//Tooltip
.tooltip {
  position: relative;
}

/* Tooltip box */
.tooltip:before {
  content: attr(title);
  position: absolute;
  width: 100px;
  background-color: v.$clr-primary;
  color: v.$clr-text;
  text-align: center;
  padding: 10px;
  line-height: 1.2;
  border-radius: 6px;
  z-index: 1;
  opacity: 0;
  transition: opacity .6s;
  bottom: 125%;
  left: 50%;
  margin-left: -60px;
  font-size: 0.75em;
  visibility: hidden;
}

/* Tooltip arrow */
.tooltip:after{
  content: "";
  position: absolute;
  bottom: 75%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  opacity: 0;
  transition: opacity .6s;
  border-color: #062B45 transparent transparent transparent;
  visibility: hidden;
}

.tooltip:hover:before, .tooltip:hover:after {
  opacity: 1;
  visibility: visible;
}


.link {
  color: v.$clr-text;
  text-decoration: none;
  border-bottom: 2px solid transparent;
  transition: border-bottom ease-in-out .3s;

  &:hover {
    border-color: v.$clr-text;
  }
}


//buttons
.button__icon {
  font-size: 1.4em;
  margin-left: v.$mb-05;
}

button {
  border-width: 0;

  &:hover {
    cursor: pointer;
  }
}

.link__button {
  transition-duration: .4s;

  &:active {
    transform: translateY(4px);
  }
}

.btn {
  font-weight: bolder;
  padding: 12px 28px;
  border-radius: v.$radius;
  transition-duration: .4s;
  border: 0.15em solid v.$clr-text;
  color: v.$clr-text;
  box-shadow: v.$fx-dropShadow;
  /*background-color: v.$clr-secondary;*/
  &:active {
    color: v.$clr-bg;
    transform: translateY(4px);
    box-shadow: none;
    /*border-radius: .9em;*/
    border: 0.15em solid transparent;
  }

  &:hover {
    background-color: v.$clr-text;
    color: v.$clr-bg;
    box-shadow: 0 10px 16px 0 rgba(0, 0, 0, .2), 0 13px 30px 0 rgba(0, 0, 0, .14);
  }

  &:focus {
    box-shadow: none;
  }
}

.btn__contrast {
  border: 0.17em solid transparent;
  color: v.$clr-bg;
  background-color: v.$clr-text;

  &:hover {
    background-color: v.$clr-bg;
    color: v.$clr-text;
  }
}

//Switches Checkbox
.toggle {
  position: relative;
  display: block;
  width: 42px;
  height: 24px;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transform: translate3d(0,0,0);
  &:before{
    content: "";
    position: relative;
    top: 1px;
    left: 1px;
    width: 40px;
    height: 22px;
    display: block;
    background: v.$clr-third;
    border-radius: 12px;
    transition: background .2s ease;
  }
  span {
    position: absolute;
    top: 0;
    left: 0;
    width: 24px;
    height: 24px;
    display: block;
    background: white;
    border-radius: 50%;
    box-shadow: 0 2px 6px rgba(#9A9999,.77);
    transition: all .2s ease;
    svg {
      margin: 7px;
      fill: none;
      path{
        stroke: #9A9999;
        stroke-width: 2;
        stroke-linecap: round;
        stroke-linejoin: round;
        stroke-dasharray: 24;
        stroke-dashoffset: 0;
        transition: all .5s linear;
      }
    }
  }
}

.cbx:checked + .toggle {
  &:before {
    background: v.$clr-primary;
  }
  span{
    transform: translateX(18px);
    path {
      stroke: v.$clr-primary;
      stroke-dasharray: 25;
      stroke-dashoffset: 25;
    }
  }
}

.scroll-to-top-wrapper {
  // uncomment to visualize "track"
  box-sizing: border-box;
  position: absolute;
  top: 110vh;
  right: 4.5vw;
  bottom: 0;
  width: auto;
  pointer-events: none;
}

.scroll-to-top {
  display: none;
  pointer-events: all;
  position: sticky;
  justify-content: center;
  align-items: center;
  bottom: 14vh;
  cursor: pointer;
  width: 20px;
  height: 20px;
  padding: 20px;
  z-index: v.$z-fixed;
  transition-duration: .4s;
  background-color: v.$clr-primary;
  border-radius: v.$radius;
  box-shadow: v.$fx-dropShadow;
  top: calc(100vh - 5rem);

  &:hover {
    border-radius: v.$radius + 1rem;
  }

  &:active {
    background-color: v.$clr-bg;
    transform: translateY(3px);
    box-shadow: none;
  }

  @media screen and (min-width: v.$bp-tablet) {
    display: none;
  }

  @media screen and (min-width: v.$bp-tablet) {
    bottom: 5vh;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.scroll__arrow {
  font-size: 2em;
  color: white;
  padding: 0;
}

.glow-on-hover {
  padding: 12px 28px;
  border-radius: v.$radius;
  transition-duration: .25s;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, .2), 0 6px 20px 0 rgba(0, 0, 0, .12);
  outline: none;
  background: transparent;
  cursor: pointer;
  position: relative;
  z-index: 0;
}

.glow-on-hover:before {
  content: '';
  background: linear-gradient(50deg, transparent, #2C4A81, v.$clr-primary, v.$clr-primary, #22c0fa, #488fc7, #274377, transparent);
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(7px);
  width: calc(100% + 6px);
  height: calc(100% + 6px);
  animation: glowing 20s linear infinite;
  opacity: 0.15;
  transition: all .4s ease-in-out;
  border-radius: v.$radius;
}

.glow-on-hover:active:before {
  top: -4px;
  left: -4px;
  filter: blur(23px);
}

.glow-on-hover:hover:before {
  opacity: 1;
  filter: blur(9px);
}

.glow-on-hover:hover:after {
  top: -3px;
  left: -3px;
  width: calc(100% + 6px);
  height: calc(100% + 6px);
  background: v.$clr-secondary;
  border: 0.15em solid v.$clr-bg;
  filter: blur(15px);
}

.glow-on-hover:after {
  z-index: -1;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  border-radius: v.$radius;
}

@keyframes glowing {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

.gradient-border {
  --border-width: 3.5px;

  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 150px;
  color: v.$clr-text;
  background: v.$clr-background;
  border-radius: var(--border-width);

  &::before {
    position: absolute;
    content: "";
    top: calc(-1 * var(--border-width));
    left: calc(-1 * var(--border-width));
    z-index: -1;
    width: calc(100% + var(--border-width) * 2);
    height: calc(100% + var(--border-width) * 2);
    background: linear-gradient(
                    60deg,
                    #2C4A81,
                    v.$clr-primary,
                    #488fc7,
                    v.$clr-primary,
                    #22c0fa,
                    #274377
    );
    background-size: 300% 300%;
    border-radius: calc(2 * var(--border-width));
    animation: moveGradient 6s alternate infinite;
  }
}

@keyframes moveGradient {
  10% {
    background-position: 0 10%;
  }
  40% {
    background-position: 100% 50%;
  }
  70% {
    background-position: 80% 30%;
  }
  100% {
    background-position: 100% 50%;
  }
}

@keyframes rotateBG {
  0% { transform: rotate(0) }

  100% { transform: rotate(360deg) }
}